<template>
    <div
    id="articleModal"
    ref="articleModal"
    class="modal fade"
    aria-labelledby="articleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 id="articleModalLabel" class="modal-title">
            <span></span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row g-2 mb-2">
              <div class="col-12">
                <label for="title" class="form-label"></label>
                <input id="title" type="text" class="form-control" placeholder=""
                v-model="itemArticle.title"
                />
              </div>
              <div class="col-12">
                <label for="subtitle" class="form-label"></label>
                <input id="subtitle" type="text" class="form-control" placeholder=""
                v-model="itemArticle.description"
                />
              </div>
          </div>
          <div class="row g-2">
            <div class="col-6">
              <div class="mb-3">
                  <label for="imageUrl" class="form-label"></label>
                  <div class="input-group mb-2">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=" URL "
                      ref="imageUrl"
                    />
                    <button
                      type="button"
                      class="btn btn-outline-secondary"
                      @click="imageUpload('imageUrl')"
                    >
                      
                    </button>
                  </div>
                  <div class="input-group">
                  <input
                    class="form-control"
                    type="file"
                    id="fileInput"
                    ref="fileInput"
                  />
                  <button
                    type="button"
                    class="btn btn-outline-secondary"
                    @click="imageUpload"
                  >
                    
                  </button>
                </div>
              </div>
              <div class="border rounded-2" style="height: 150px; background-size: cover; background-position: center center;"
              :style="{backgroundImage: `url(${itemArticle.image})`}">
              </div>
            </div>
            <div class="col-6 d-flex flex-column justify-content-between">
              <div class="mb-2">
                <label for="tag" class="form-label"></label>
                <input id="tag" type="text" class="form-control" placeholder=" [ ENTER ] "
                 v-model="tag" @keyup.enter="addTag(tag)"
                />
              </div>
                <div class="d-flex flex-wrap">
                  <button class="d-flex align-items-center btn btn-secondary btn-sm rounded-3 me-1 mb-1"
                  v-for="(tag, index) in itemArticle.tag" :key="tag" @click="removeTag(index)">
                    <span>{{ tag }}</span>
                    <i class="material-icons-round fs-5 ms-1">clear</i>
                  </button>
                </div>
              <div class="mb-2">
                <label for="author" class="form-label"></label>
                <input id="author" type="text" class="form-control" placeholder=""
                v-model="itemArticle.author"
                />
              </div>
              <div>
                <label for="create_at" class="form-label"></label>
                <input id="create_at" type="date" class="form-control"
                v-model="create_at"/>
              </div>
            </div>
          </div>
          <hr>
          <div class="mb-2">
            <ckeditor :editor="editor" :config="editorConfig" v-model="itemArticle.content"></ckeditor>
          </div>
          <div class="form-check me-3">
                  <input
                    id="isPublic"
                    class="form-check-input"
                    type="checkbox"
                    v-model="itemArticle.isPublic"
                    :true-value="true"
                    :false-value="false"
                  />
                  <label class="form-check-label" for="isPublic"
                    ></label
                  >
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-secondary"
            data-bs-dismiss="modal"
          >
            
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="updateArticle(itemArticle.id)"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic'

export default {
  props: ['temp-article', 'is-new'],
  data () {
    return {
      articleModal: '',
      itemArticle: {
        tag: []
      },
      create_at: '',
      fileInput: '',
      imageUrl: '',
      tag: '',
      editor: ClassicEditor,
      editorConfig: {
        toolbar: ['heading', 'bold', 'italic', 'blockquote', 'link', '|', 'numberedList', 'bulletedList', '|', 'undo', 'redo']
      }
    }
  },
  methods: {
    // / 
    updateArticle (articleId) {
      // Vue  DOM  ProductModal  props 
      //  nextTick()  DOM 
      this.$nextTick(() => {
        let url = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_PATH}/admin/article/${articleId}`
        let httpMethod = 'put'

        if (this.isNew === true) { //  'editPublic' 
          url = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_PATH}/admin/article`
          httpMethod = 'post'
        }
        this.$http[httpMethod](url, { data: this.itemArticle })
          .then((res) => {
            //  Modal
            this.closeArticleModal()

            //  
            this.$emit('get-articles')//  emit
          })
          .catch((err) => {
            console.dir(err.response)
          })
      })
    },
    // 
    imageUpload (imageUrl) {
      // 
      if (imageUrl === 'imageUrl') {
        this.itemArticle.image = this.imageUrl.value
        //  input 
        this.imageUrl.value = ''
        return
      }

      // 
      const file = this.fileInput.files[0]
      //  formData
      const formData = new FormData()
      formData.append('file-to-upload', file)
      const url = `${process.env.VUE_APP_URL}/api/${process.env.VUE_APP_PATH}/admin/upload`
      this.$http.post(url, formData)
        .then((res) => {
          this.itemArticle.image = res.data.imageUrl
          //  input 
          this.fileInput.value = ''
        })
        .catch((err) => {
          console.log(err.response)
        })
    },
    // 
    addTag (tag) {
      this.itemArticle.tag.push(tag)
      this.tag = ''
    },
    // 
    removeTag (index) {
      if (window.event.target.nodeName === 'I') {
        this.itemArticle.tag.splice(index, 1)
      }
    },
    //  modal
    openArticleModal () {
      this.articleModal.show()
    },
    //  modal
    closeArticleModal () {
      this.articleModal.hide()
    }
  },
  watch: {
    tempArticle () {
      this.itemArticle = { ...this.tempArticle }

      if (this.isNew === 'editPublic') {
        this.itemArticle.isPublic = !(this.tempArticle.isPublic)
      }
      //  YYYY-MM-DD
      this.create_at = new Date(this.itemArticle.create_at * 1000).toISOString().split('T')[0]
    },
    create_at () {
      //  YYYY-MM-DD  Unix 
      // Math.floor() 
      this.tempArticle.create_at = Math.floor(new Date(this.create_at) / 1000)
    }
  },
  mounted () {
    this.articleModal = new Modal(this.$refs.articleModal, { keyboard: false, focus: false })
    this.fileInput = this.$refs.fileInput
    this.imageUrl = this.$refs.imageUrl
  }
}
</script>

<style>
.ck-editor__editable_inline {
  min-height: 300px;
}

/*  link  Modal  focus  */
:root {
    --ck-z-default: 100;
    --ck-z-modal: calc( var(--ck-z-default) + 999 );
}
</style>
